body{margin:0;font-family:interstate, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html,body,main{margin:0;padding:0}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}.container{display:grid;grid-template-columns:-webkit-min-content 1fr;grid-template-columns:min-content 1fr;grid-template-rows:1fr;grid-template-areas:"picker map";grid-gap:0;min-height:100vh;max-width:calc(100vw - 4rem)}.container .picker{grid-area:picker}.container svg{grid-area:map;padding:2rem}

.map,svg{max-width:100vw;max-height:100vh}.map text,.map path,.map circle,svg text,svg path,svg circle{transition:0.5s ease-in-out}.map .tp,svg .tp{cursor:pointer}

.picker{width:20rem;background:#222;color:#fff;display:flex;flex-flow:column nowrap;height:100%}.picker button{background:none;border:none;color:inherit;font-family:interstate, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1.5rem;text-align:left;display:flex;flex-flow:row nowrap;align-items:center}.picker button i{font-size:2rem;margin-right:0.25rem}.picker ul{list-style:none;padding:1rem 1rem}.picker ul li{margin-bottom:1rem}.picker .points-of-interest{width:19rem;background:#0071ce;position:fixed;top:0;bottom:0;left:-19rem;transition:left 0.2s ease-out;display:flex;flex-flow:column nowrap}.picker .points-of-interest .title{display:flex;flex-flow:row nowrap;align-items:center;padding:0 1rem;margin:1rem 0}.picker .points-of-interest .title button{background:#fff;border:none;color:#0071ce;margin:0 0.5rem 0 0;padding:0.125rem;border-radius:50%;display:flex;flex-flow:row nowrap;align-items:center;justify-content:center;width:2.25rem;height:2.25rem}.picker .points-of-interest .title button i{font-size:2rem;margin:0}.picker .points-of-interest .title h2{margin:0;font-size:1.5rem}.picker .points-of-interest.open{left:0;box-shadow:2px 0 8px rgba(255,255,255,0.15);transition:left 0.2s ease-in}.picker .points-of-interest button{font-size:1.25rem}.picker .points-of-interest button .material-icons{font-size:1em;align-self:flex-start}.picker .points-of-interest-list{flex:1 1 auto;margin:0}

.picker .clear-button{background:#fff;color:#222;margin:0 1rem 1rem 1rem;border:none;display:block;padding:0.5rem 1rem;font-size:1.25rem;font-family:interstate, sans-serif;font-weight:400;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center}.picker .clear-button:hover{text-decoration:underline}.picker .clear-button:active,.picker .clear-button:focus{text-decoration:none}

